<template>
  <div class="use">
    <div class="topheader">
      <div class="img">
        <img :src="backgroundUrl" alt="" />
      </div>
      <div class="title">
        <div class="img">
          <img :src="avatarUrl" alt="" />
        </div>
        <div class="content">
          <p>{{ nickname }}</p>
          <span>关注 {{ newFollows }}</span
          ><span class="line">|</span><span>粉丝 {{ followeds }}</span
          ><br />

          <span class="rank">Lv{{ level }}</span>
        </div>
      </div>
      <div class="button">
        <span
          class="iconfont icon-zuojiantou"
          @click="$router.push('/recommend')"
        ></span>
      </div>
    </div>
    <div class="musicList">
      <van-tabs v-model="active">
        <van-tab title="主页">
          <h3>基本信息</h3>
          <div class="info">
            <p>村龄: {{ createDays }}天 ({{ year }}年{{ month }}月注册)</p>
            <p>地区: 广东省广州市</p>
            <span @click="detailedshow = true"
              >更多信息<van-icon name="arrow"
            /></span>
          </div>
          <div class="content">
            <div class="love">
              <div class="icon">
                <van-icon class="icos" name="like" />
              </div>
              <div class="detail">
                <p class="lovemusic">我喜欢的音乐</p>
                <p class="meusicnum">{{likelist.length}}首</p>
              </div>
            </div>
          </div>
        </van-tab>
        <van-tab title="动态">
          <div class="moving">暂时还没有动态哦</div>
        </van-tab>
      </van-tabs>
    </div>

    <div class="detailed" v-show="detailedshow">
      <div class="top" @click="detailedshow = false">
        <van-icon name="arrow-left" />
        <span>基本信息</span>
      </div>
      <div class="personal-info">
        <h3>个人信息</h3>
        <p>等级: {{ level }}</p>
        <p>性别: {{ (gender = 0 ? "女" : "男") }}</p>
        <p>地区: 广东省 广州市</p>
      </div>
      <div class="personal-introduce">
        <h3>个人介绍</h3>
        <p>还没有填写个人介绍</p>
      </div>
    </div>
  </div>
</template>

<script>
import { getUserinfo,getlikelist,getaccount } from "../api/userinfo"; //getaccountinformation
export default {
  data() {
    return {
      active: 2,
      detailedshow: false, //更多信息
      id:null,
      backgroundUrl: null, //背景图片
      avatarUrl: null, //头像
      nickname: null, //用户名
      newFollows: null, //关注
      followeds: null, //粉丝
      level: null, //等级
      createDays: null, //注册时常
      gender: 0, //性别 0女 1男
      year: null, //注册年
      month: null, //注册月
      likelist:1,//喜欢的歌曲数据
    };
  },
  methods: {
    //用户信息
    getUserinfoFun() {
      getUserinfo({ uid: this.id }).then((data) => {
        this.backgroundUrl = data.profile.backgroundUrl;
        this.avatarUrl = data.profile.avatarUrl;
        this.nickname = data.profile.nickname;
        this.newFollows = data.profile.newFollows;
        this.followeds = data.profile.followeds;
        this.level = data.level;
        this.createDays = data.createDays;
        this.gender = data.profile.gender;
        let bindings = data.bindings[0].refreshTime;
        let date = new Date(bindings * 1000);
        this.year = date.getFullYear();
        this.month = date.getMonth() + 1;
      });
    },
    // 账号信息
    // getaccountinformationFun() {
    //   getaccountinformation().then((data) => {
    //     console.log(data);
    //   });
    // },
    // 获取喜欢的歌曲 
    getlikelistFun(){
      getlikelist({uid:this.id}).then(data=>{
        this.likelist=data.ids
      })
    }
  },
  created() {
    this.id = window.localStorage.getItem("uid");
    this.getUserinfoFun();
    // this.getaccountinformationFun();
    this.getlikelistFun()
    getaccount().then(data=>{
      console.log(data);
    })
  },
};
</script>

